.navigation, .header_btn {
  display: none;
}

.container-header {
  min-height: 37px;
  justify-content: space-between;
  align-items: center;
  padding-top: 12px;
  display: flex;
  position: relative;
}

.logo-img {
  width: 40px;
  height: 37px;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  background-color: var(--main-color);
  transition: transform var(--time-duration) var(--timing-function);
  z-index: 50;
  padding-bottom: 64px;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.mobile-menu-open-btn, .mobile-menu-close-btn {
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0 20px 0 0;
  padding: 0;
  display: inline-flex;
}

.menu-toggle {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.menu-container__overall {
  margin-bottom: 51px;
  padding-top: 12px;
  padding-left: 32px;
  padding-right: 20px;
  display: flex;
  position: relative;
}

.menu-container__bottom {
  flex-direction: column;
  align-items: center;
  padding-left: 28px;
  padding-right: 28px;
  display: flex;
}

.mobile-menu {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.mobile-menu__link {
  text-align: center;
  color: var(--main-text-color);
  transition: color var(--time-duration) var(--timing-function);
  font-size: 32px;
  font-weight: 700;
  line-height: 1.22;
}

.mobile-menu__link:hover, .mobile-menu__link:focus {
  color: var(--second-text-color);
}

.mobile-menu__item + .mobile-menu__item {
  margin-top: 32px;
}

.mobile-menu-close-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 10px;
  right: 10px;
}

.mobile-header_btn {
  min-width: 152px;
  height: 42px;
  text-align: center;
  cursor: pointer;
  background-color: var(--accent-color);
  transition: color var(--time-duration) var(--timing-function);
  transition: background-color var(--time-duration) var(--timing-function);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  margin-top: 110px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
}

.mobile-header_btn:hover {
  color: var(--second-text-color);
  background-color: var(--hover-color);
}

.mobile-header_btn:focus {
  color: var(--main-text-color);
  background-color: var(--hover-color);
}

