.container{    
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 auto;

outline: 1px solid tomato;

@include tel {
    width: 320px;
  }

  @include mobile {
    width: 480px;
  }

  @include tablet {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @include desktop {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}